.main-nav__toggle {
  display: none; }
  @media (max-width: 767px) {
    .main-nav__toggle {
      position: relative;
      display: block;
      padding: 22px 22px;
      border: none;
      cursor: pointer;
      background-color: transparent; } }

@media (max-width: 767px) {
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 24px;
    right: 15px;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd; } }

@media (max-width: 767px) {
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 20px;
    right: 12px;
    width: 19px;
    height: 1.5px;
    background-color: #011c40; }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none; }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg); } }

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none; }
  .main-nav--opened .main-nav__list {
    display: block;
    padding: 18px 15px;
    flex-direction: column;
    grid-column: 1 / 3; }
  .main-nav--opened {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-content: flex-start;
    width: 285px;
    height: 568px;
    margin: 0;
    margin-left: auto;
    padding-top: 10px;
    background-color: #f9fbfd; }
    .main-nav--opened::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 35px;
      height: 100%;
      box-shadow: -35px 0 0 0 rgba(0, 43, 102, 0.5); }
  .main-nav--opened .main-nav__link {
    padding: 14px 11px;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #011c40; }
  .main-nav--opened .main-nav__link--logo {
    padding: 8px 15px 9px;
    grid-column: 1 / 2; }
  .main-nav--opened .main-nav__link-icon {
    fill: #011c40; }
  .main-nav--opened .main-nav__toggle {
    grid-column: 2 / 3; }
  .main-nav--nojs .main-nav__list {
    display: block; }
  .main-nav--nojs .main-nav__toggle {
    display: none; }
  .main-nav--nojs {
    position: static; }
  .main-nav--nojs {
    display: grid;
    row-gap: 10px;
    align-content: flex-start;
    width: 320px;
    height: 568px;
    padding: 20px 15px;
    background-color: #f9fbfd; }
  .main-nav--nojs .main-nav__link {
    padding: 13px 11px;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #011c40; }
  .main-nav--nojs .main-nav__link--logo {
    padding: 8px 15px 9px;
    grid-column: 1 / 2; }
  .main-nav--nojs .main-nav__link-icon {
    fill: #011c40; }
  .main-nav--nojs .main-nav__list {
    grid-row: 2 / 3;
    padding: 0 10px; } }

